import React, { useState, useEffect } from "react";
import { useForm } from "react-hook-form";
import clinicApi from "api/clinic";

import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  Grid,
  Typography,
  Divider,
} from "@mui/material";
import SuiButton from "components/SuiButton";

export default function ModalVeterinario({ open, onClose, onSave, initialData, id }) {
  const LABEL_FONT = "1.25rem";
  const INPUT_FONT = "1.05rem";
  const TITLE_FONT = "1.25rem";

  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm({
    defaultValues: {
      email: "",
      password: "",
      telefono: "",
      nombre: "",
    },
  });

  const [submitError, setSubmitError] = useState("");

  useEffect(() => {
    // Mirror ModalEditarMascota behaviour: only reset when dialog opens to avoid flicker.
    if (!open) return;

    let mounted = true;
    async function fetchData() {
      if (initialData) {
        reset({
          email: initialData.email || initialData.user?.email || "",
          password: "",
          telefono: initialData.telefono || "",
          nombre: initialData.nombre || initialData.user?.nombre || "",
        });
        return;
      }

      if (!id) {
        reset({ email: "", password: "", telefono: "", nombre: "" });
        return;
      }

      try {
        const data = await clinicApi.retrieve("veterinarios", id);
        if (!mounted) return;
        reset({
          email: data.email || data.user?.email || "",
          password: "",
          telefono: data.telefono || "",
          nombre: data.nombre || data.user?.nombre || "",
        });
      } catch (err) {
        // eslint-disable-next-line no-console
        console.error("Error fetching veterinario in create modal:", err);
        if (mounted) reset({ email: "", password: "", telefono: "", nombre: "" });
      }
    }

    fetchData();

    return () => {
      mounted = false;
    };
  }, [initialData, id, open, reset]);

  const submit = async (data) => {
    setSubmitError("");
    try {
      await onSave?.(data);
      reset();
      onClose?.();
    } catch (err) {
      setSubmitError(err?.message || "Error al guardar");
    }
  };

  const textFieldSx = {
    "& .MuiInputLabel-root": { fontSize: LABEL_FONT, fontWeight: 400 },
    "& .MuiFormHelperText-root": { fontSize: "1.2rem" },
    "& .MuiOutlinedInput-root": { minHeight: 48 },
    "& .MuiInputBase-input": { fontSize: INPUT_FONT, padding: "12px 12px" },
  };

  return (
    <Dialog
      open={open}
      onClose={onClose}
      fullWidth
      maxWidth="sm"
      PaperProps={{
        sx: {
          borderRadius: 3,
          p: 0,
        },
      }}
    >
      <DialogTitle
        sx={{
          fontWeight: 600,
          textAlign: "center",
          pb: 1,
          fontSize: TITLE_FONT,
        }}
      >
        Registrar Veterinario
      </DialogTitle>

      <Divider />

      <form onSubmit={handleSubmit(submit)}>
        <DialogContent sx={{ pt: 2 }}>
          <Grid container spacing={1}>
            <Grid item xs={12}>
              <TextField
                label="Email"
                fullWidth
                size="small"
                type="email"
                {...register("email", {
                  required: "El email es obligatorio",
                  maxLength: { value: 300, message: "M√°ximo 255 caracteres" },
                })}
                error={!!errors.email}
                helperText={errors.email?.message}
                sx={textFieldSx}
                InputLabelProps={{ shrink: true }}
              />
            </Grid>

            <Grid item xs={12}>
              <TextField
                label="Password"
                fullWidth
                size="small"
                type="password"
                {...register("password", { required: "La contrase√±a es obligatoria" })}
                error={!!errors.password}
                helperText={errors.password?.message}
                sx={textFieldSx}
                InputLabelProps={{ shrink: true }}
              />
            </Grid>

            <Grid item xs={12}>
              <TextField
                label="Tel√©fono"
                fullWidth
                size="small"
                {...register("telefono", {
                  maxLength: { value: 50, message: "M√°ximo 50 caracteres" },
                })}
                sx={textFieldSx}
                InputLabelProps={{ shrink: true }}
              />
            </Grid>

            <Grid item xs={12}>
              <TextField
                label="Nombre"
                fullWidth
                size="small"
                {...register("nombre", {
                  maxLength: { value: 255, message: "M√°ximo 255 caracteres" },
                })}
                sx={textFieldSx}
                InputLabelProps={{ shrink: true }}
              />
            </Grid>

            {submitError && (
              <Grid item xs={12}>
                <Typography color="error" fontSize="0.88rem">
                  {submitError}
                </Typography>
              </Grid>
            )}
          </Grid>
        </DialogContent>

        <Divider />

        <DialogActions sx={{ p: 2, justifyContent: "flex-end", gap: 1 }}>
          <SuiButton
            variant="outlined"
            buttonColor="secondary"
            onClick={onClose}
            sx={{ fontSize: INPUT_FONT, textTransform: "none", px: 3, minWidth: 120, height: 44 }}
          >
            Cancelar
          </SuiButton>
          <SuiButton
            variant="gradient"
            buttonColor="dark"
            type="submit"
            sx={{ fontSize: INPUT_FONT, textTransform: "none", px: 3, minWidth: 120, height: 44 }}
          >
            Guardar
          </SuiButton>
        </DialogActions>
      </form>
    </Dialog>
  );
}
